.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-row-center {
  @extend .flex-row;
  justify-content: center;
}

.flex-row-center-y,
.flex-row-center-cross {
  @extend .flex-row;
  align-items: center;
}

.flex-row-column-center {
  @extend .flex-row-center;
  justify-content: center;
  align-items: center;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.flex-column-center {
  @extend .flex-column;
  justify-content: center;
}

.flex-column-center-cross {
  @extend .flex-column;
  align-items: center;
}

.flex-column-row-center {
  @extend .flex-column;
  justify-content: center;
  align-items: center;
}
