.migrateandrecover {
  &-tab-box {
    position: sticky;
    top: 0;
    z-index: 9;
  }
  &-migrate-copy-btn {
    display: block;
    width: 100%;
  }
  &-hint-box {
    padding: 0 15px 15px;
    line-height: 1.5;
    color: #888888;
