@import '../../styles/defined';

.detail {
  padding-bottom: 50px;

  &-header {
    position: sticky;
    top: 0;
    padding: 15px;
    background: $bg_default;
    box-shadow: 0 2px 3px 0px rgba(0, 0, 0, 0.05);
    z-index: 99;
  }

  &-title {
    font-size: 17px;
    margin-bottom: 6px;
  }

  &-sub-title {
    font-size: 14px;
  }

  &-record-title {
    font-size: 17px;
  }

  &-record-info {
    font-size: 14px;
    color: #333;
  }

  &-footer {
    margin: 15px 0;
    text-align: center;
  }
}
