@import '../../styles/defined';
.index {
  padding-bottom: 50px;
  &-header {
    position: sticky;
    top: 0;
    font-size: 17px;
    color: #333;
    margin: 0 0 8px;
    z-index: 2;
    background: $bg_default;
    box-shadow: 0 0 5px 1px rgba($color: #666666, $alpha: 0.1);
    &-title {
      padding: 15px 15px 9px;
    }
  }
  &-list {
    &-item-title {
      padding: 5px 0;
    }
    &-item-active {
      background-color: rgba($color: #666666, $alpha: 0.1);
    }
    &-item-pressing {
      background-color: rgba($color: #333333, $alpha: 0.1);
    }
    .am-list-body {
      &::after,
      &::before {
        display: none !important;
      }
    }
  }

  &-add {
    bottom: 160px;
    transform: rotate(45deg);
  }

  &-modal-input-box {
    margin: 10px 15px;
  }
}
